import React from "react"
import "./cube.scss"
import { useState } from "react"

export const Cube = ({ front, back }) => {
  const [cubeClass, setCubeClass] = useState("cube")
  const toggle = () => {
    cubeClass === "" ? setCubeClass("cube-back") : setCubeClass("")
  }
  return (
    <div className="scene-wrapper">
      <div className="scene">
        <div className={"cube " + cubeClass}>
          <div className="cube__face cube__face--front">
            <button onClick={() => toggle()}>sample</button>
            {front}
          </div>
          <div className="cube__face cube__face--back">
            <button onClick={() => toggle()}>bpm</button>
            {back}
          </div>
          <div className="cube__face cube__face--right">right</div>
          <div className="cube__face cube__face--left">left</div>
          <div className="cube__face cube__face--top">top</div>
          <div className="cube__face cube__face--bottom">bottom</div>
        </div>
      </div>
    </div>
  )
}
